import axios from 'axios'
import React, { useState } from 'react'
import Tilt from 'react-tilt'
import '../msc/Contact.css'
import MsgIcon from '../image/img-01.png'
export const ContactusForm = () => {
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [phone, setPhone] = useState("")
    const [message, setMessage] = useState("")
    const [server, setServer] = useState(false)
    const [errorM, setErrorM] = useState("")
    const [sucess, setSucess] = useState("")
    const submitForm = () => {

        if (name === "" || email === "" || phone === "" || message === "") {
            setErrorM("You missed some field!!")
        }
        else {
            setErrorM("")
            try {
                setServer(true)
                axios.post('https://kaihatsu-club.herokuapp.com/api/contact', {
                    "name": name,
                    "email": email,
                    "phone": phone,
                    "message": message
                }).then(res => {
                    setServer(false)
                    setName("")
                    setEmail("")
                    setPhone("")
                    setMessage("")
                    setSucess('We recived your Response!!')
                    console.log(res)
                }).catch(e => {
                    setServer(false)
                    console.log(e)
                    setErrorM("Error!! Please Contact Your Administration")
                })
            } catch {
                setErrorM("Something Went Wrong")
            }
        }
    }
    return (
        <div className="bg-contact100">
            <div className="container-contact100" >
                <div className="wrap-contact100"  style={{backgroundColor:"transparent"}}>

                   <div className="col-md-5" 
                   style={{marginBottom:"20vh",marginTop:"15vh"}}
                   >
                   <Tilt className="Tilt" options={{ max: 50 }} style={{ height: 250, width: 250 }} >
                        <div className="contact100 Tilt-inner" data-tilt>
                            <img src={MsgIcon} alt="IMG" />
                        </div>
                    </Tilt>
                   </div>
                 <div className="col-md-6">
                 <form className="contact100-form">
                        <div style={{ color: "green" }}>
                            {sucess}
                        </div>
                        <div style={{ color: "red" }}>
                            {errorM}
                        </div>
                        <span className="contact100-form-title" style={{color:"#fff"}}>
                            Get in touch.
                        </span>
                        <div className="wrap-input100 " >
                            <input className="input100" type="text" name="name" placeholder="Name" value={name} onChange={(e) => { setName(e.target.value) }} />
                            <span className="focus-input100" />
                            <span className="symbol-input100">
                                <i className="fa fa-user" aria-hidden="true" />
                            </span>
                        </div>
                        <div className="wrap-input100 " >
                            <input className="input100" type="text" name="contact" placeholder="Phone" value={phone} onChange={(e) => setPhone(e.target.value)} />
                            <span className="focus-input100" />
                            <span className="symbol-input100">
                                <i className="fa fa-phone" aria-hidden="true" />
                            </span>
                        </div>
                        <div className="wrap-input100 " >
                            <input className="input100" type="text" name="email" placeholder="Email" value={email} onChange={(e) => setEmail(e.target.value)} />
                            <span className="focus-input100" />
                            <span className="symbol-input100">
                                <i className="fa fa-envelope" aria-hidden="true" />
                            </span>
                        </div>
                        <div className="wrap-input100">
                            <textarea className="input100" name="message" placeholder="Message" defaultValue={""} value={message} onChange={(e) => setMessage(e.target.value)} />
                            <span className="focus-input100" />
                        </div>
                        <div className="container-contact100-form-btn">
                            {
                                !server ? (
                                    <button className="contact100-form-btn" onClick={(e) => {
                                        e.preventDefault()
                                        submitForm()
                                    }}>
                                        Submit
                                    </button>
                                ) : <button className="contact100-form-btn server-button" disabled={true}>
                                    Processing
                                </button>
                            }
                        </div>
                    </form>
                
                 </div>
                </div>
            </div>
        </div>

    )
}