import React from 'react'
import '../msc/About.css'
import { TeamProfile } from './TeamProfile'
import Data from './Data'
import DirectorSahab from '../image/profile/directorSahab.jpg'
export const TeamCarousel = () => {

    return (
        <div style={{}}>
            <div id="carouselExampleIndicators" className="carousel slide aboutBackground team-desktop" 
            
            data-ride="carousel">
                <ol className="carousel-indicators" >
                    <li data-target="#carouselExampleIndicators" data-slide-to={0} className="carousel-indecator-team" style={{marginTop:"-0vh"}} className="active" />
                    <li data-target="#carouselExampleIndicators" data-slide-to={1} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={2} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={3} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={4} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={5} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={6} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={7} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={8} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={9} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={10} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={12} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={12} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                    <li data-target="#carouselExampleIndicators" data-slide-to={13} className="carousel-indecator-team" style={{marginTop:"-0vh"}} />
                </ol>
                <div className="carousel-inner">
                    <div className="carousel-item active" data-interval="3500" >
                        <TeamProfile data={{
                            "name": "Dr Surendra Rahmatkar",
                            "post": "Chief Advisor",
                            "image": DirectorSahab,
                            "bio": "The club has established with the guidance and blessings of Dr Prof Surendra Rahmatkar, Director Amity School Engineering & Technology. He has been instrumental and pivotal in the establishment and growth of the club. With his visionary leadership skills and excellent mentoring skills the club is destined to excel and achieve new heights under him.    ",
                            "linkedin":"drsurendra"
                        }} />
                    </div>
                    {
                        Data.map((res, i) => {
                            return (
                                <div className="carousel-item" data-interval="3500" key={i}>


                                    <TeamProfile data={res} />


                                </div>
                            )
                        })
                    }

                </div>
                <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true" />
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true" />
                    <span className="sr-only">Next</span>
                </a>
            </div>




        </div>

    )
}
