import React from 'react'

export const TeamProfile = ({ data }) => {
    return (



        <div class="text-center animate__animated animate__backInLeft" style={{ paddingTop: 100 }}>

            <a href={`https://www.linkedin.com/in/${data.linkedin}`} target="new">
                <img src={data.image} alt="testimonial" className="center-block" width="260px" height="260px" style={{ borderRadius: "50%", border: "solid", borderColor: "#129cf3" }} />
            </a>

            <div className="testimonial-caption">
                <h2>{data.name}</h2>
                <h4>{data.post}</h4>
                <div className="teams-bio" style={{ color: "#fff" }}>{data.bio}</div>
            </div>
        </div>













    )

}