import React from 'react'
import { UpcomingEvents } from '../components/UpcomingEvents'
import { Navbar } from '../helper/Navbar'
import '../msc/notice.css'
export const Notice=()=>{
    React.useEffect(() => {
        document.title = "Kaihatsu | Events" ;
      });
    return(
    <div style={{minHeight: "100vh" }} className="uE animate__animated animate__fadeIn">
        <Navbar colorScheme="transparent"/>
        <UpcomingEvents/>
    </div>
    )
}