import React from 'react'
import { ContactusForm } from '../components/ContactusForm'
import { Navbar } from '../helper/Navbar'
import '../msc/main.css'
import '../msc/Contact.css'
export const Contact=()=>{
    React.useEffect(() => {
        document.title = "Kaihatsu | Contact Us" ;
      });
    return(
        <div className="head-contact bg-contact100bg animate__animated animate__jackInTheBox" style={{}}>
        <Navbar colorScheme="transparent" darkTheme={false}/>
        <ContactusForm/>
    </div>
    )
}